@use '../../../../global/styles/commons';

$footer-height: var(--footer-height, 1.75rem);
$header-background-dark: var(
  --header-background-dark,
  var(--header-background)
);

:host {
  display: block;
}

.footer-container {
  container: footer-variant-simple / inline-size;
}

.footer.variant-simple {
  display: flex;
  gap: 1rem;
  align-items: center;
  min-height: $footer-height;
  background: var(--layer);

  .start {
    display: flex;
    align-items: center;
  }
}

@container footer-variant-simple (min-width: 768px) {
  .footer-container {
    .variant-simple {
      flex-direction: row;
      padding: var(--spacing-05) var(--spacing-07);

      .start {
        flex: 1;
      }
    }
  }
}

@container footer-variant-simple (max-width: 767px) {
  .footer-container {
    .variant-simple {
      flex-direction: column;
      padding: var(--spacing-05) var(--spacing-05);

      .slot-container {
        width: 100%;
      }
    }
  }
}
